
import PsLabel from '@/components/core/label/PsLabel.vue';
import PsDropdown from '@/components/core/dropdown/PsDropdown.vue';

export default {
    name:"PsTimePicker",
    props : {
        hour :{
            type: String,
            default: "0"
        },
        min :{
            type: String,
            default: "0"
        },
        ampm :{
            type: String,
            default: "1"
        },
        isDisabled : {
            type: Boolean,
            default: false
        }
    },
    components : {
        PsLabel,
        PsDropdown
    },
    setup(_, {emit}) {
        function getHour(hour) {  
         
            if(hour == null || hour == '') {
                hour = '0';
            }
            return hour;   
        }
        function handleHourInput(v) {      
            emit('update:hour', v);
        }
        function getMinute(min) {  
           
            if(min == null || min == '') {
                min = '0';
            }
            return min;   
        }
        function handleMinuteInput(v) {      
            emit('update:min', v);
        }
        function getAmPm(ampm) {  
        
            if(ampm == null || ampm == '') {
                ampm = '1';
            }
            return ampm;   
        }
        function handleAmPmInput(v) {      
            emit('update:ampm', v);
        }

        return {
            getHour,
            handleHourInput,
            getMinute,
            handleMinuteInput,
            getAmPm,
            handleAmPmInput
        }
    },
    
}
